import React from 'react';
import Container from 'react-bootstrap/Container';
import Tab from 'react-bootstrap/Tabs';
import Tabs from 'react-bootstrap/Tabs';
import '../index.css';

import FolioItem from './FolioItem';
import Spacer from './Spacer';

import foodii_big_grid from '../assets/images/big_grid/food_big_grid.png';
import gige_big_grid from '../assets/images/big_grid/gige_big_grid.png';
import planchat_big_grid from '../assets/images/big_grid/planchat_big_grid.png';
import decibel_big_grid from '../assets/images/big_grid/decibel_big_grid.png';

import robson_big_grid from '../assets/images/big_grid/robson_big_grid.png';
import band_big_grid from '../assets/images/big_grid/band_big_grid.png';
import square_big_grid from '../assets/images/big_grid/square_big_grid.png';

import food_bgrid_logo from '../assets/images/big_grid/food_bgrid_logo.png';
import gige_bgrid_logo from '../assets/images/big_grid/gige_bgrid_logo.png';
import planchat_bgrid_logo from '../assets/images/big_grid/planchat_bgrid_logo.png';
import decibel_bgrid_logo from '../assets/images/big_grid/decibel_bgrid_logo.png';

import honey_big_grid from '../assets/images/big_grid/honey_big_grid.png';
import monument_big_grid from '../assets/images/big_grid/monument_big_grid.png';
import sketches_big_grid from '../assets/images/big_grid/sketches_big_grid.png';

import foodii_dgrid from '../assets/images/big_grid/dgrid/food_dgrid.png';
import planchat_dgrid from '../assets/images/big_grid/dgrid/planchat_dgrid.png';
import gige_dgrid from '../assets/images/big_grid/dgrid/gige_dgrid.png';

import robson_dgrid from '../assets/images/big_grid/dgrid/robson_dgrid.png';
import band_dgrid from '../assets/images/big_grid/dgrid/band_dgrid.png';
import monument_dgrid from '../assets/images/big_grid/dgrid/monument_dgrid.png';

function DescriptionGrid(props) {
  return (
    <Container className="">
    <Spacer />

    <Tabs defaultActiveKey="DigitalWork" className="imagetab">

    <Tab eventKey="DigitalWork" title="Digital">
      <Container>
      <div class="row">
        <div class="col-sm dgrid-image">
          <FolioItem imagesrc={foodii_dgrid} destination="/foodpage"/>
          <Spacer />
        </div>
        <div class="col-sm">
          <div className="dgridtextrow">
            <p className="dgrid-title">Foodii</p>
            <p className="dgrid-text">Designing navigational architecture to support
            an iterative process of searching, browsing, and comparing.</p>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-sm dgrid-image">
          <FolioItem imagesrc={planchat_dgrid} destination="/planchatpage"/>
          <Spacer />
        </div>

        <div class="col-sm">
        <div className="dgridtextrow">
        <p className="dgrid-title">PlanChat</p>
        <p className="dgrid-text">Analyzing the gaps in current digital hangout-planning tools</p>
        </div>
        </div>
      </div>

      <div class="row">
        <div class="col-sm dgrid-image">
          <FolioItem imagesrc={gige_dgrid} destination="/Project1Page"/>
          <Spacer />
        </div>
        <div class="col-sm">
        <div className="dgridtextrow">
        <p className="dgrid-title">GIGE</p>
        <p className="dgrid-text">Designing web-based lead generation and ecommerce solutions at
        GIGE Corporation</p>
        </div>
        </div>
      </div>

      </Container>
    </Tab>


    {/*main tabs physical, all work, and digital */}


    <Tab eventKey="PhysicalWork" title="Physical">
    <Container>
    <div class="row">
      <div class="col-sm dgrid-image">
        <FolioItem imagesrc={robson_dgrid} destination="/robsonpage"/>
        <Spacer />
      </div>
      <div class="col-sm">
      <div className="dgridtextrow">
        <p className="dgrid-title">Robson Square</p>
        <p className="dgrid-text">Conducting an architectural heuristic analysis on present and future
        issues facing a public square in Vancouver.</p>
      </div>
      </div>
    </div>

    <div class="row">
      <div class="col-sm dgrid-image">
        <FolioItem imagesrc={band_dgrid} destination="/bandpage"/>
        <Spacer />
      </div>
      <div class="col-sm">
      <div className="dgridtextrow">
      <p className="dgrid-title">Guitarist Chair</p>
      <p className="dgrid-text">Prototyping and building an ergonomic seating solution for performing guitarists.</p>
      </div>
      </div>
    </div>

    <div class="row">
      <div class="col-sm dgrid-image">
        <FolioItem imagesrc={monument_dgrid} destination="/monumentpage"/>
        <Spacer />
      </div>
      <div class="col-sm">
      <div className="dgridtextrow">
      <p className="dgrid-title">Monument</p>
      <p className="dgrid-text">Studying design systems: the component, the design guidelines, and the hollistic whole.</p>
      </div>
      </div>
    </div>


    </Container>
    </Tab>



    </Tabs>

    </Container>





  );
}

export default DescriptionGrid;
