import React from 'react';
import Psummary from '../components/Psummary';
import Spacer from '../components/Spacer';
import FullImage from '../components/FullImage';
import HeroImage from '../components/HeroImage';
import ScrollTop from '../components/ScrollTop';
import Container from 'react-bootstrap/Container';
import BackButton from '../components/BackButton';
import CenteredBlurb from '../components/CenteredBlurb';

import monumentconstruction from '../assets/images/monument/monumentconstruction.png';
import multiview from '../assets/images/monument/multiview.png';
import toy_rendering from '../assets/images/monument/toy_rendering.jpg';
import toy_45 from '../assets/images/monument/toy_45.jpg';
import monumenthero from '../assets/images/monument/monumenthero.png';

function MonumentPage(props) {

  return (
    <div>
    <div className="divmovedown">
      <BackButton />
      <Container>

      <FullImage imagesource={monumenthero}/>
      <Spacer />
      <Spacer />

      <Container className="generaltextcontainer">
        <p className="herotext">The Monument</p>
        <p className="subtitletext">A study on design systems: the component, the design guidelines, and the whole.</p>
        <p>The Monument is a formal study on the relationships among the piece,
        the collection, and the whole. The study takes existing objects - screws,
        shampoo bottle caps, and pieces from Lego's 'Bionicle' toys, and rearranges them
        into a novel and beautiful object. Through meticulous attention to composition
        and symmetry, a chaotic grouping of unrelated items can become a unified whole that is
        greater than the sum of its parts.</p>
      </Container>

      <div className="CenteredLink">
        <a className="ExternalLink" href="https://www.daniels.utoronto.ca/work/student-work/architectural-representation-1-arc221/aaron-lo">
        Featured on the University of Toronto Architecture Faculty Website</a>
      </div>
            <HeroImage imagesource={monumentconstruction}/>
      <Spacer />
      <Spacer />
      <HeroImage imagesource={multiview}/>
      <Spacer />
      <Spacer />

      </Container>


      <ScrollTop />
      </div>
    </div>
  );
}

export default MonumentPage
