import React from 'react';
import Psummary from '../components/Psummary';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import FullImage from '../components/FullImage';
import ScrollTop from '../components/ScrollTop';
import Menu2 from '../components/Menu2';
import HeroImage from '../components/HeroImage';
import CenteredTitle from '../components/CenteredTitle';
import Container from 'react-bootstrap/Container';
import BackButton2 from '../components/BackButton2';
import Divider from '../components/Divider';
import SizeableImage from '../components/SizeableImage';
import '../index.css';

import functioned from '../assets/images/food/functioned.png';
import defined from '../assets/images/food/defined.jpg';
import scenario2 from '../assets/images/food/scenario2.jpg';
import choose from '../assets/images/food/choose.png';
import page_architecture from '../assets/images/food/page_architecture.jpg';
import wireframes from '../assets/images/food/wireframes.png';
import food_hero2 from '../assets/images/food/food_hero2.png';
import food_hero3 from '../assets/images/food/food_hero3.jpg';
import prototyping from '../assets/images/food/prototyping.jpg';
import journey2 from '../assets/images/food/journey2.png';
import journey3 from '../assets/images/food/journey3.png';
import personas from '../assets/images/food/personas.jpg';
import finalscreens1 from '../assets/images/food/finalscreens1.png';
import fontcolor from '../assets/images/food/fontcolor.jpg';
import food_hero5 from '../assets/images/food/food_hero5.png';
import admockups from '../assets/images/food/admockups.jpg';
import gif1 from '../assets/images/food/food_gif1.gif'

import journeycompiled from '../assets/images/food/journey_compiled.png';
import food_interview from '../assets/images/food/food_interview.png';
import finalscreens2 from '../assets/images/food/finalscreens2.jpg';

import personajane from '../assets/images/food/persona_jane.png';
import explorewireframe from '../assets/images/food/explore_wireframe.png';

import initial_architecture from '../assets/images/food/food_initial_architecture.png';
function FoodPage(props) {

  return (
    <div>
    <div className="divmovedown">
    <BackButton2/>

        <div id="Summary" className="anchor"/>
        <Container>

        <HeroImage imagesource={food_hero5}/>

        <Spacer />

        <Container className="generaltextcontainer">
          <p className="herotext">Foodii Mobile App</p>
          <p className="subtitletext">Designing a solution to help with the age old problem: "where should we eat?"</p>
          <p>Deciding where to go for a meal is an iterative and often messy process that involves
          searching, browsing, and making comparisons among choices. Foodii is a mobile app that uses a tabbed navigation system
          to help users through this task.<br/><br/> The scope of this project included ideation,
          user research, information architecture design, prototyping, and usability testing.</p>
        </Container>
        <Divider />
        <Container className="generaltextcontainer">
          <p className="paragraphtitle">Literature research</p>
          <p>This project began with research on existing literature to gain a high level understanding of
          why this problem has become so universally widespread. The major factors that were
          discovered to contribute to the problem were:
          <br/><br/>
          <b>1. Choice Overload</b>
          <br/>
          Choice overload, or overchoice, is a psychological phenomenon introduced
          by American writer Alvin Toffler in 1970. It states that decision making
          is more difficult when a person is presented with too many options. With the
          enormous amount of different restaurants available to us, particularly
          in dense urban areas, it becomes clear how this phenomenon could contribute to our
          inability to make a decision.
          <br/><br/>
          <b>2. The criteria that we use are constantly changing</b>
          <br/>
          The issue of having too many choices is compounded by the fact that we use a wide
          range of criteria to decide on a restaurant - for instance, its type of cuisine,
          price, and location. These criteria constantly change and are also affected by our
          previous choices (perhaps you do not want to eat steak twice in a row for
            lack of variety).
          </p>
        </Container>
        <Divider />
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">Discovery interviews</p>
        <p>With the high level research completed, user interviews were conducted with the goal of
        discovering the specific processes that users go through when deciding where to eat.
        Specifically, the interviews aimed to uncover insights into their mental process, the the tools that they use,
        the factors that they consider when comparing options,
        and common pain points that they face.</p>
        <br/>
        <p className="paragraphtitle">Key findings</p>
        <p>After results were gathered, the data was codified and thematic anaylsis was used to articulate the
        most dominant themes. The major insights that were identified from this were:
        <br/><br/>
        1. Users often begin the process by mentally recalling the 'usual list' of familiar options. If a decision cannot
        be reached with this, they will use online tools to search for new options that they have recently heard, or browse for
        new options online. Restaurants are then compared and a decision is reached. <br/><br/>
        2. Common tools that are used during the process include Google Maps to search for nearby options and
        Yelp to browse for new and trending options.<br/><br/>
        3. A common frustration of the process was that on some occasions people
        don't have any specific criteria in mind, resulting aimless browsing and "walking in circles"<br/><br/>
        4. The criteria that users weight when making the decision included the restaurant's
        location, its cuisine, its price, and its reviews. The importance of each criteria depended on the respondent's
        specific situation (where they were, how much time they had).<br/><br/>


        </p>
        </Container>
        <SizeableImage imagesource={defined} width="75%"/>
        <Spacer/>
        <Divider />
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">Articulating user goals</p>
        <p>From the research, it was clear that people use an iterative process of recalling known restauraunts,
        searching and browsing for new options, and comparing possible choices. Therefore in order to help with this process,
        the app would need to provide the tools to achieve the goals:<br/><br/>
        1. Quickly make multiple search and browse queries using a variety of criteria.<br/><br/>
        2. Track options for comparison.<br/><br/>

        Based on these goals, it was apparent that a solution focused on information architecture would be most appropriate. Specifically, a <b>shallow
        navigational heirarchy</b> would need to be used to help with the iterative search, browse, and compare actions. To guide this process
        user personas were therefore developed, one of which is displayed below:


        </p>
        </Container>
        <Spacer/>
        <SizeableImage imagesource={personajane} width="100%"/>
        <Spacer/>
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">Diagramming User Journeys</p>
        <p>
        Using the developed personas, user journeys were articulated and consoliated into the diagram below, which
        maps out the steps that users take when selecting a restauraunt.
        <br/><br/>
        From this, it was discovered that the 'choosing process' often composes of a
        repeating cycle of identifying a restaurant option, assessing it, editing a 'mental shortlist', refining
         criteria, and making new queries.
        </p>
        </Container>
        <Spacer/>
        <SizeableImage imagesource={choose} width="70%"/>

        <Spacer/><Spacer/>
        <Divider/>
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">Designing the Information Architecture</p>
        <p>In order to assist users with the iterative process of 'choosing', a shallow navigational heirary was designed.
        Specifically, it was important that users are able to make new queries and compare restauraunts without needing to
        navigate back and forth in deep page heirarchies. <br/><br/>
        The architecture of the solution places all of these tasks on the highest level, allowing users to navigate back and forth
        between making new searches and comparing options efficiently.



        </p>
        </Container>
        <Spacer/>
        <SizeableImage imagesource={page_architecture} width="90%"/>
        <Spacer/><Spacer/><Spacer/>
        <SizeableImage imagesource={journeycompiled} width="95%"/>
        <Spacer/><Spacer/>
        <Divider/>
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">Wireframing the interfaces</p>
        <p>On the interface level, the solution was designed as a <b>tabbed navigation system</b> that
        allowed users to navigate among making searches, browsing with various criteria, and editing a shortlist, all while
        being able to quickly switch between the tasks.
        </p>
        </Container>
        <SizeableImage imagesource={explorewireframe} width="40%"/>
        <Spacer /><Spacer />
        <SizeableImage imagesource={wireframes}/>
        <Container className="generaltextcontainer">
        <p className="paragraphtitle">Usability A/B testing two interface options</p>
        <p>Digital prototypes were developed in order to conduct usability testing
        of the tabbed navigation system. Two versions were made, where the shortlist
        was accessed either through a dedicated tab at the top of the app, or via an
        icon on the bottom tab bar.
        <br/><br/>
        Users were asked to complete a series of tasks designed to compare their
        effectiveness for browsing restaurants and modifying the shortlist. The metrics that
        were used to determine success included the completion rate, completion time, and error rate.
        The data from the study indicated that the version where the shortlist was accessed via the top
        tab provided a more effective solution, as it resulted in a 17% higher completion rate and a 27% reduced
        error rate.

        </p>
        </Container>
        <SizeableImage imagesource={prototyping} width="98%"/>
        <Spacer/>



        <Divider />
        <CenteredBlurb title="App Overview"/>

        <HeroImage imagesource={food_hero2}/>

        <Container className="generaltextcontainer">
          <p className="captiontitle">Tabbed navigation system</p>
          <p className="captiontext">The tabbed navigation system allows users to cycle between new
          search queries, pages for browsing, and their shortlist of restaurants
          </p>
        </Container>
        <Divider/>
        </Container>
        <Spacer /><Spacer/>

        <SizeableImage imagesource={food_hero3} width="90%"/>
        <Container className="generaltextcontainer">
          <p className="captiontitle">Order or get directions directly from the restaurant page</p>
          <p className="captiontext">
          </p>
        </Container>
        <Spacer/><Spacer/>
        <div id="Research" className="anchor"/>


        <div id="Diagramming" className="anchor"/>
        <div id="Mapping" className="anchor"/>


        <div id="Personas" className="anchor"/>
        <div id="Architecture" className="anchor"/>


        <div id="Prototyping" className="anchor"/>



        <div id="Testing" className="anchor"/>





        <ScrollTop />
    </div>
    </div>
  );
}

export default FoodPage
