import React from 'react';
import Psummary from '../components/Psummary';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import FullImage from '../components/FullImage';
import ScrollTop from '../components/ScrollTop';
import Menu3 from '../components/Menu3';
import HeroImage from '../components/HeroImage';
import CenteredTitle from '../components/CenteredTitle';
import Container from 'react-bootstrap/Container';
import BackButton2 from '../components/BackButton2';
import HeroLeftRight from '../components/HeroLeftRight';
import SizeableImage from '../components/SizeableImage';
import Divider from '../components/Divider';
import BackButton from '../components/BackButton';

import '../index.css';

import calendar_animation from '../assets/images/planchat/calendar_animation.gif';
import current_photoalbum from '../assets/images/planchat/current_photoalbum.png';
import edit_itinerary from '../assets/images/planchat/edit_itinerary.png';
import event_album from '../assets/images/planchat/event_album.png';
import event_animation from '../assets/images/planchat/event_animation.gif';
import itinerary_chat from '../assets/images/planchat/itinerary_chat.png';
import itinerary_timeline from '../assets/images/planchat/itinerary_timeline.png';
import new_event from '../assets/images/planchat/new_event.png';
import new_photoalbum from '../assets/images/planchat/new_photoalbum.png';
import past_events from '../assets/images/planchat/past_events.png';
import pcaccount_page from '../assets/images/planchat/pcaccount_page.png';
import pclogo_1 from '../assets/images/planchat/pclogo_1.png';
import pclogo_2 from '../assets/images/planchat/pclogo_2.png';
import pclogo_3 from '../assets/images/planchat/pclogo_3.png';
import pclogo_4 from '../assets/images/planchat/pclogo_4.png';
import planapp_architecture from '../assets/images/planchat/planapp_architecture.png';
import planchat_hero from '../assets/images/planchat/planchat_hero.png';
import planchat_wireframe from '../assets/images/planchat/planchat_wireframe.png';
import planchat_event_new from '../assets/images/planchat/event_animation_new.gif';
import pc_core from '../assets/images/planchat/pc_core.png';
import pc_quote from '../assets/images/planchat/planchat_quote.png';
import pc_dc from '../assets/images/planchat/pc_dcsolution.png';
import pc_fb from '../assets/images/planchat/pc_fbsolution.png';

function PlanchatPage(props) {

  return (
    <div>
    <div className="divmovedown">
        <BackButton2/>

          <div id="Summary" className="anchor"/>
          <Container>
          <HeroImage imagesource={planchat_hero}/>


          <Container className="generaltextcontainer">
            <p className="herotext">PlanChat Mobile App</p>
            <p className="subtitletext">The problem of planning hangouts online: "Sorry I missed the updates, when are we meeting again?"</p>
            <p>
            This project was prompted after I started noticing recurring issues when planning hangout online. Often, plans would be
            discussed through chat-based social media such as Discord, WhatsApp groups, or text groups. While these platforms are effective
            for discussing what to do, there would always be confusion about what the plan actually was - either people missed important updates,
            or there were simply too many changes to keep track of. There had to be a better solution!

            <br/><br/>
            The focus on this project was to conduct research to articulate the shortcomings of the current chat tools in faciltating hangout planning.
            User interviews and competitive analysis were used to this end. Then, a digital solution was designed by integrating an itinerary directly into
            a chatroom.

            </p>
          </Container>
          <Divider />
          <Container className="generaltextcontainer">
            <p className="paragraphtitle">User interviews to identify gaps in current hangout planning tools</p>
            <p>This project began with conducting user interviews to gain an initial understanding
            of the issue. The focuses of the interviews was to find out the platforms that people
            commonly use, their strategies for keeping everybody on the same page,
            and any pain points that they have in the process.
            </p>
            <p className="paragraphtitle">Findings</p>
            <p>
            Thematic analysis was conducted on the responses to identify key themes presented by the respondents.
            The key findings that were discovered from the interviews were:
            <br/><br/>
            1. Common platforms that were used included Discord and Facebook Events. The latter is often reserved
            for larger events of over 10 people. <br/><br/>

            2. In Discord, events are often planned using a dedicated "hangouts" Text Channel to avoid planning discussion
             being overwhelmed by general discussion. In addition, Pinned Messages are often used to summarize the itinerary.<br/><br/>

            3. In Facebook Events, updates and polls on the Event Page would be used to track updates, while discussion would take place
            in a seperate chatroom dedicated to the event. <br/><br/>

            4. A common source of confusion for users who missed updates in the chat was where to find the latest information.
            Sometimes, organizers would forget to update the Pinned Messages or Facebook Event Page to reflect updates.
            In these cases, people need to manually ask for a summary.
            </p>
            <Spacer/>
            <SizeableImage imagesource={pc_quote} width="100%"/><Spacer/>
          </Container>
          <Divider />

          <Container className="generaltextcontainer">
            <p className="paragraphtitle">Analyzing Discord's Solution</p>
            <p>Discord provides two platforms to communicate through: Text Channels and Voice Channels.
            The ability to create multiple Text Channels in a Server is highly effective at separating general discussion from
            planning discussion, reducing the chance of information becoming lost.<br/><br/>
            Inclusion of Voice Channels provides a an platform for immediate communication. However, decisions made through voice
            will need to be textually summarized for users that are not present in the call. <br/><br/>
            Finally, Discord provides Pinned Messages to easily view specific messages. While this is a useful tool to keep a
            summary of the plan, respondents indicated that its reliabilty was sometimes inconsistent, as event organizers may not
            always update it immediately.
            </p>
          </Container>
          <SizeableImage imagesource={pc_dc} width="60%"/>
          <Spacer />
          <Divider/>
          <Container className="generaltextcontainer">
            <p className="paragraphtitle">Analyzing Facebook's Solution</p>
            <p>Facebook offers Facebook Event as its tool for planning hangouts.
            Unlike Discord, Event Pages specifically designed for this purpose, and therefore provides a
            more comprehensive solution with features such as a Wall to display updates, post polls, and track attendees.
            <br/><br/>
            In addition, Facebook Messenger provides a platform for group chat discussions. Like Discord, it also allows
            for Voice Chats. However, Facebook Messenger does not provide the option to divide a single Chatroom into seperate
            Text Channels, resulting in a Chatroom dedicated to the event needing to be created.
            <br/><br/>
            Similiar to Discord's Pinned Messages, one limitation of Facebook's solution is the gap between its Event Page and its Messenger platforms. While these are effective
            in their respective functions, updating the Event Page is a manual one-way process for the Event Planner. As a result, the information
            on the Event Page may not always reflect the latest decisions made in the Chatroom.
            </p>
          </Container>
          <SizeableImage imagesource={pc_fb} width="65%"/>
          <Spacer />
            <Divider />
          <Container className="generaltextcontainer">
          <p className="paragraphtitle">Key Research Finding: there exists a disconnect between the discussion of plans and the event summary that causes
          confusion in hangout planning.</p>
          <p>The research indicated that the most significant challenge of planning hangouts online
          was keeping everybody up-to-date. It is not only difficult to discuss plans in general
          due to the fact that attendees may not always be present in chatrooms, but it is also hard
          to keep track of an itinerary once a decision has been made.
          </p><br/>
            <p className="paragraphtitle">Actionable Insight: The solution should bridge the Chatroom
            and Itinerary Summary to reduce confusion about the latest plan.</p>
            <p>Development of the solution began by identifying the core tools that are required
            to plan an event. It was found that two interconnected elements would be needed: <br/><br/>
            <b>1. A chatroom</b><br/>
            A platform to discuss ideas, suggest activites, and discuss changes would be essential.
            A chatroom would be the most appropriate platform of communication due to its flexibility
            and similarity to existing tools.<br/><br/>
            <b>2. An itinerary</b><br/>
            Providing an easily accessible itinerary to users allows them to easily view the latest
            iteration of an event at a quick glance, without having to sort through pages of new messages,
            sifting through pinned messages, or asking another attendee for a summary.
            <br/><br/>
            It was essential that these two elements were integrated with one another for the solution
            to be effective - otherwise, it would simply result in more manual work for the event creator,
            who would need to manually update the itinerary every time a decision is made.
            </p>
          </Container>


          <SizeableImage imagesource={pc_core} width="60%"/><Spacer/>


          <Container className="generaltextcontainer">

          </Container>
          <Container className="generaltextcontainer">
            <p className="paragraphtitle">The event's tinerary would automatically update as changes are voted on</p>
            <p>In order to reduce manual work required by the event organizer, itinerary items would be managed by polls
            posted in the chatroom. The results of these polls would then automatically feed back into the itinerary, ensuring that it is always
             kept up-to-date for everybody to see.
            </p>
          </Container>
          <SizeableImage imagesource={edit_itinerary} width="95%"/>

          <Container className="generaltextcontainer">
            <p className="paragraphtitle">The Itinerary Timeline will summarize all the event's activites</p>
            <p>Each event would have a dedicated Itinerary Timeline, which would show all of the event's activities
            as individual itinerary items. Often, people may not be able to attend the entire event.
            By dividing an event into seperate activities, it is easy for users
            to see exactly what is happening, when it is
            happening, and who is going.
            </p>
          </Container>

          <SizeableImage imagesource={itinerary_timeline} width="65%"/>

          <Spacer/>
          <Divider />

          <Container className="generaltextcontainer">
            <p className="paragraphtitle">Mapping out the app's architecture</p>
            <p>
            It was determined that events would be managed using Event Pages. Every event would have its own individual page, where
            users can view updates and invite attendees. Furthermore, every Event Page would have its own associated Chatroom and Itinerary - changes to a plan
            can be suggested in the Chatroom using Polls, and Poll results automatically update Itinerary
            Items to be easily viewed at-a-glance.
            <br/><br/>

            Since the PlanChat app is centered around creating and managing Event Pages, the remaining architecture
            would need to make it easy to access and edit these pages.
            Existing events are naturally to easy access as they are listed on the Home Page.
            However, it was also important for users to be able to create events from various other
            pages of the app. Therefore, the design allows users to create a brand new event from (1) the Home Page and
            (2) the Chat Page. Users can also duplicate an old event from a Past Event Page.
            </p>
          </Container>
          <SizeableImage imagesource={planapp_architecture} width="90%"/>
          <Spacer/><Spacer/><Spacer/><Spacer/>
          <SizeableImage imagesource={planchat_wireframe} width="100%"/>
          <Spacer/><Spacer/><Spacer/>
          <Divider/>

          <CenteredBlurb title="App Overview"
          />

          <SizeableImage imagesource={calendar_animation} width="40%"/>

          <Container className="generaltextcontainer">
            <p className="captiontitle">The Home Page: view all of your upcoming events in a list or
            calendar layout</p>
            <p className="captiontext">The Home Page shows all of your upcoming events
            and can be viewed in two different layouts,
            making sure you never miss plans that are coming up.
            </p>
          </Container>


          </Container>

          <div id="Events" className="anchor"/>


          <Spacer />
          <Divider />

          <Spacer />
          <Container>

          <SizeableImage imagesource={planchat_event_new} width="33%"/>
          <Container className="generaltextcontainer">
            <p className="captiontitle">The Event Page</p>
            <p className="captiontext">The Event Page is the hub that connects the Itinerary with
            the Chatroom. 
            </p>
          </Container>
          </Container>
          <Divider />
          <Spacer/><Spacer/>
          <SizeableImage imagesource={new_event} width="75%"/>

          <Container className="generaltextcontainer">
            <p className="captiontitle">Creating a new event</p>
            <p className="captiontext">Events are easily created by tapping the plus icon on the bottom
            menu bar.
            </p>
          </Container>

          <Container className="generaltextcontainer">
            <p className="herotext">Thoughts Moving Forward</p>
            <p className="paragraphtitle">Applying the solution as a feature to existing apps</p>
            <p>While this iteration of PlanChat was designed as a new mobile app, the solution can instead be
            applied as a feature into existing platforms as well. For example, the core idea of integrating an automatically-updating
            itinerary with chat discussions can instead be introduced directly into Discord Text Channsl or Facebook Messenger.
            Future iterations of this concept could expore these possibilities.
            </p>
          </Container>
          <div id="Research" className="anchor"/>
          <div id="Architecture" className="anchor"/>

          <div id="Branding" className="anchor"/>

          <ScrollTop />


    </div>
    </div>
  );
}

export default PlanchatPage
