import React from 'react';
import Psummary from '../components/Psummary';
import HeroLeftRight from '../components/HeroLeftRight';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import HeroImage from '../components/HeroImage';
import MenuIos from '../components/MenuIos';
import ScrollTop from '../components/ScrollTop';
import CenteredTitle from '../components/CenteredTitle';
import BackButton from '../components/BackButton';
import Container from 'react-bootstrap/Container';
import SizeableImage from '../components/SizeableImage';
import Divider from '../components/Divider';

import readingbench from '../assets/images/readingbench/reading_bench.png';
import readingdetail from '../assets/images/readingbench/readingdetail.png';
import readingparts from '../assets/images/readingbench/readingparts.png';
import readingwide from '../assets/images/readingbench/readingwide.png';

import bandmain from '../assets/images/bandchair/bandmain.png';
import banddetail1 from '../assets/images/bandchair/banddetail1.png';
import banddetail2 from '../assets/images/bandchair/banddetail2.png';
import bandoverall from '../assets/images/bandchair/bandoverall.png';

import banddiagram from '../assets/images/bandchair/band_diagram.png';
import bandprototype from '../assets/images/bandchair/band_prototype.jpg';

function BandPage(props) {

  return (
    <div>
    <BackButton/>
    <Container>

    <HeroLeftRight imagesource={bandmain} width="100%"
    title="Guitarist Chair"
    text="14in (L) x 16in (W) x 26in (H)"

    />
    <Container className="generaltextcontainer">
      <p className="herotext">Guitarist Chair</p>
      <p className="subtitletext">Prototyping an ergonomic chair for performing guitarists</p>
      <p>When performing while seated, guitarists rest their instrument on their lap, often while also
      needing to position themselves to sing into a microphone. This combination of requirements
      often results in uncomfortable positions when using seating designed for restauraunt tables, bar tables, or
      lounges. <br/><br/> The goal of this project was to design and build an ergonomic solution to be used
      for performing guitarists.</p>
    </Container>
    <Divider/>
    <Container className="generaltextcontainer">
      <p className="subtitletext">Experimenting with the foot rest positioning</p>
      <p>The distance between the foot rest and seat top was the main variable to be examined.
      If the distance is too small, it would result in an uncomfortable knee position and a compressed diaphragm, making it difficult to sing.
      Conversely, if the distance is too large, the angle of the user's thigh would not be condusive to resting the instrument comfortably.
       </p>
    </Container>
    <Spacer/><Spacer/>
    <SizeableImage imagesource={banddiagram} width="75%"/>


    <Container className="generaltextcontainer">
      <p className="subtitletext">The prototype</p>
      <p>A prototype was created using a stool, two wood clamps, and scrap lumber. In order to manipulate the distance between the foot rest and seat top,
      wood planks were be added or removed, with the variable being recorded using a tape measure. A guitar and microphone stand were also used to replicate
      the primary use case, increasing the ecological validity of the results.<br/><br/>
      Qualitative feedback on the comfort of each iteration was collected, and a distance of 15 1/2" was determined to be optimal for the solution.
       </p>
    </Container>

    <SizeableImage imagesource={bandprototype} width="50%"/>

    <Spacer/>
    </Container>

    <Container className="generaltextcontainer">
      <p className="subtitletext">A modern and minimal design</p>
      <p>The design of the chair was heavily inspired by Mid-Century Modernism, using a split-top and tapered legs to maintain a light and minimal feel.
      The seat top was cut to display the natural grain pattern of the pine wood, bringing emphasis to the natural beauty of the material.
       </p>
    </Container>

    <Container>
    <HeroImage imagesource={banddetail1}/>
    <Spacer />
    <HeroImage imagesource={banddetail2}/>
    <Spacer />
    <HeroImage imagesource={bandoverall}/>
    </Container>
    <ScrollTop />
    </div>
  );
}

export default BandPage
