import Hero from '../components/Hero';
import Content from '../components/Content';
import Psummary from '../components/Psummary';
import LeftRight from '../components/LeftRight';
import CenteredBlurb from '../components/CenteredBlurb';
import Spacer from '../components/Spacer';
import FullImage from '../components/FullImage';
import ImgTab from '../components/ImgTab';
import HeroImage from '../components/HeroImage';
import ScrollTop from '../components/ScrollTop';
import Container from 'react-bootstrap/Container';
import React, { Component } from 'react';
import BackButton from '../components/BackButton';
import SizeableImage from '../components/SizeableImage';
import Divider from '../components/Divider';

import robson_hero from '../assets/images/robson/robson_hero.png';
import robsondiagram from '../assets/images/robson/robsondiagram.png';
import robsonfuture from '../assets/images/robson/robsonfuture.png';
import robsonheat from '../assets/images/robson/robsonheat.png';
import robsonplan from '../assets/images/robson/robsonplan.png'
import robsonsec from '../assets/images/robson/robsonsec.png';
import robsonview from '../assets/images/robson/robsonview.png';
import robsonanalysis from '../assets/images/robson/robsonanalysis.png';

class RobsonPage extends Component {

  componentDidMount() {
  window.scrollTo(0,0);
}

  render() {

    return (
    <div>
      <BackButton />
      <Container>
      <Spacer/>
      <Spacer/>
      <Spacer/>
      <HeroImage imagesource={robson_hero}/>

      </Container>
      <Spacer /><Spacer /><Spacer />
      <Container className="generaltextcontainer">
        <p className="herotext">Revitalizing Robson Square</p>
        <p className="subtitletext">Conducting a heuristic analysis on present and future issues facing a public square in Vancouver</p>
        <p>Vancouver is projected to face myriad social and environmental
        pressures in the near future. Within the next 50 years, the city's population
        is predicted to double in density. Furthermore, temperatures and heat wave frequency
        will also increase due to climate variability. For public gathering spaces such as Robson Square,
        these pressures will result in issues of congested circulation and undesirably hot
        open spaces. This masterplan aimed to address these problems
        while activating the space as a lively multi-layered public square.</p>
      </Container>
      <Divider />
      <Container className="generaltextcontainer">
        <p className="paragraphtitle">Identifying the most vulnerable areas</p>
        <p>This project began with an analysis on the areas of Robson Square that would be most affected by current and future envrionmental and sociological issues.
        Some of the the most pressing issues that were identified were:
        <br/><br/>
        1. Rising temperatures and increasingly intense heatwaves will cause open areas to become uninviting as gathering spaces.<br/><br/>
        2. Population increase will create strain on circulation both within and across the park.<br/><br/>
        3. Closures of other public areas will increase reliance on Robson Square as a central public space of the downtown core.
        </p>
      </Container>
      <SizeableImage imagesource={robsonanalysis} width="100%"/>
      <Spacer/><Spacer/><Spacer/>
      <Container className="generaltextcontainer">
      <p className="paragraphtitle">The proposed solution: a vine-covered canopy bridge</p>
      <p>The proposed design is a vine-covered canopy bridge that would cover
      the entirety of the square. Firstly, this pulls the circulation up off the
      ground plane, alleviating it to become a dedicated and flexible gathering area.
      Furthermore, the vine canopy will shield the area below from the sun,
      creating a pleasant semi-shaded space. Finally, the existing roof over
      the ice rink will be removed, which enables passing pedestrians on the
      bridge to view the activity occuring below.
      </p>
      </Container>
      <HeroImage imagesource={robsonheat}/>
      <Spacer />
      <HeroImage imagesource={robsonview}/>
      <Spacer />
      <Spacer />


      <Spacer /><Spacer />
      <Container>
      <HeroImage imagesource={robsondiagram}/>
      </Container>

      <Spacer />
      <Spacer />
      <Spacer />
      <HeroImage imagesource={robsonplan}/>
      <Spacer />
      <Spacer />
      <Spacer />
      <HeroImage imagesource={robsonsec}/>


      <ScrollTop />
    </div>
  )
  }
}

export default RobsonPage
